import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import {TextField, Button} from "@material-ui/core";
import {Container} from "./IndexStyledComponent";

class Index extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            userData: {},
            id: 3
        };
        this.onChangeUserId = this.onChangeUserId.bind(this);
        this.getUserData = this.getUserData.bind(this);
        this.setUserData = this.setUserData.bind(this);
    }

    onChangeUserId(event) {
        this.setState({
            id: event.target.value
        });
    }

    async getUserData() {
        const URL = `https://reqres.in/api/products/${this.state.id}`;
        const responses = await fetch(URL);
        responses.json().then(response => {
            console.log("Response ", response);
            this.setUserData(response.data);
        });
        // let xhr = new XMLHttpRequest();
        // xhr.open("GET", `https://reqres.in/api/products/${this.state.id}`, true);
        // xhr.onload = function(){
        //     console.log(xhr.responseText);
        //     alert(JSON.parse(xhr.responseText).data.name);
        //     this.setUserData(JSON.parse(xhr.responseText).data);
        // };
        // xhr.send();

        // let client = new XMLHttpRequest();
        // client.open("GET", `https://reqres.in/api/products/${this.state.id}`, true);
        // client.onreadystatechange = function () {
        //     if (this.readyState === XMLHttpRequest.DONE) {
        //         if (this.status === 314) {
        //             console.info("Authentication required, Redirecting");
        //             try {
        //                 this.setUserData(JSON.parse(client.responseText).data);
        //             } catch (newLocationErr) {
        //                 console.error('error in finding new location');
        //             }
        //         } else if(this.status === 0 || this.status / 100 === 4) {
        //             alert(`Server is down`);
        //             console.info(`Server is down`);
        //         } else {
        //             console.error("Something went wrong");
        //         }
        //     }
        // };
        // client.send();
    }

    setUserData(userData) {
        this.setState({
            userData: userData
        });
    }

    componentDidMount () {
        console.log("component mounted making api call..");
        this.getUserData();
      }

    render() {
        return (
            <Container>
                <React.Fragment>
                    <TextField onChange={this.onChangeUserId}></TextField>
                    <Button onClick={this.getUserData} variant="contained" color="primary">Get User Data</Button>
                    {
                        !this.state.userData ? null : <React.Fragment>
                            <div>ID: {this.state.userData.id}</div>
                            <div>Name: {this.state.userData.name}</div>
                            <div>Year: {this.state.userData.year}</div>
                            <div>Pantone: {this.state.userData.pantone_value}</div>
                        </React.Fragment>
                    }
                </React.Fragment>
            </Container>
        );
    }
}
ReactDOM.render(<Index />, document.getElementById('root'));



